.contact-list {
  display: none;
}

@media screen and (min-width: 768px) {
  .contact-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: auto;

    & .contact-link:hover,
    & .contact-link:focus {
      fill: $accent-color;
      color: $accent-color;
    }
  }

  .contact-link {
    display: flex;
    color: $primary-text-color;
    font-weight: 500;
    letter-spacing: 0.02em;
    text-decoration: none;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact-list {
    justify-content: flex-end;
    padding-top: 21px;
    padding-bottom: 21px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact-link {
    font-size: 12px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact-item:nth-child(1) {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .contact-item:nth-child(2) {
    margin-left: 50px;
  }
  .contact-link {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}

.contact-link svg {
  margin-right: 10px;
  fill: currentColor;
}
